import React, { FunctionComponent } from "react";
import { useGetLocale } from "providers";
import { Helmet } from "react-helmet";

import { ProgressiveModal } from "components";
import { repeat } from "utils";
import { Accordion } from "components";

import { ModalsProps } from "config/routes";
import { DialogHeader } from "pages/ModalPages/componentsProgressive";
import Template from "pages/ModalPages/componentsProgressive/Template";

import css from "./style.module.scss";

const Help: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const intl = useGetLocale();

  return (
    <ProgressiveModal open={isOpen} width={530}>
      <Helmet>
        <title>FAQ | Crypto Gambling Casino</title>
      </Helmet>
      <DialogHeader
        className={css.title}
        title={intl.formatMessage({ id: "help.title" })}
      />

      <Template>
        <Accordion
          className={css.accordion}
          contents={repeat(5, (i) => ({
            summary: intl.formatMessage({ id: `help.questions.${i + 1}.q` }),
            details: intl.formatMessage({ id: `help.questions.${i + 1}.a` }),
          }))}
          summaryClasses={{
            summary: css.question,
            details: css.text,
          }}
        />
      </Template>
    </ProgressiveModal>
  );
};

export default Help;
